import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import businessSelect from '@/components/common-components/businessSelect';
import { isHasFunc } from '@/until/index';
import banner from '@/components/common-components/bannerSlide';
let payAdmin = class payAdmin extends Vue {
    constructor() {
        super(...arguments);
        this.raceLength = 0;
        this.raceCost = 0;
        this.accountList = [];
        this.accountId = '';
        this.accountInfo = {};
    }
    // 计算属性
    getAccountId(id, accountInfo) {
        this.accountId = id;
        this.$set(this, 'accountInfo', accountInfo);
    }
    toOtherPage(type) {
        if (type) {
            let path = '/' + type;
            this.$router.push({ path: path, query: { accountId: this.accountId } });
        }
    }
    getPower(key) {
        let user = this.$store.state.userInfo;
        return isHasFunc(key, user);
    }
    get showAdmin() {
        if (this.$store.state.userInfo && this.$store.state.userInfo.power && this.$store.state.userInfo.power.length > 0) {
            return this.$store.state.userInfo.adminAccount.indexOf(this.accountId) > -1;
        }
        else {
            return false;
        }
    }
};
payAdmin = __decorate([
    Component({
        components: {
            businessSelect,
            banner
        },
        filters: {
            fenToYuan(num) {
                if (typeof num !== 'number' || isNaN(num))
                    return null;
                return '¥' + (num / 100).toFixed(2);
            }
        }
    })
], payAdmin);
export default payAdmin;
